import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    tasks: [],
    url: "http://localhost:3000",
  },
  getters: {
    tasks: (state) => {
      return state.tasks;
    },
    url: (state) => state.url + "/tasks",
  },
  mutations: {
    updateTask: (state, payload) => {
      const { id } = payload;
      const taskIndex = state.tasks.findIndex((i) => i.id === id);
      state.tasks[taskIndex] = payload;
      state.tasks = JSON.parse(JSON.stringify(state.tasks));
    },
  },
  actions: {
    fetchTasks({ state, getters }) {
      const url = getters.url;
      axios.get(url).then((data) => {
        state.tasks = data.data;
      });
    },

    addTask({ getters, dispatch }, payload) {
      axios.post(getters.url, payload).then(() => {
        dispatch("fetchTasks");
      });
    },
    updateTask({ getters, dispatch, commit }, payload) {
      const { id, ...rest } = payload;
      commit("updateTask", payload);
      axios
        .put(`${getters.url}/${id}`, rest)
        .then(() => {})
        .catch(() => {
          dispatch("fetchTasks");
        });
    },
    deleteTask({ getters, dispatch, state }, payload) {
      const { id } = payload;
      const taskIndex = state.tasks.findIndex((i) => i.id === id);
      state.tasks.splice(taskIndex, 1);
      axios
        .delete(`${getters.url}/${id}`)
        .then(() => {})
        .catch(() => {
          dispatch("fetchTasks");
        });
    },
  },
  modules: {},
});
